/* Palette of Border Colors */
/* Palette of Feedback Colors */
:host {
  --pfe-status--BackgroundColor:  var(--pfe-theme--color--feedback--default, #606060);
  --pfe-broadcasted--color--text: var(--pfe-theme--color--feedback--default--lightest, #ececec);
  --pfe-status--TextTransform: uppercase;
  background-color: var(--pfe-status--BackgroundColor);
  color: var(--pfe-broadcasted--color--text);
  position: fixed;
  right: 0;
  top: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 3rem;
}

:host([hidden]) {
  display: none;
}

.flag {
  transform: rotateZ(90deg);
}

.flag > span {
  display: inline-block;
  font-weight: bold;
  text-transform: var(--pfe-status--TextTransform);
  font-size: 0.875rem;
  line-height: 1em;
  white-space: nowrap;
}

:host([pfe-status="moderate"]) {
  --pfe-status--BackgroundColor:  var(--pfe-theme--color--feedback--moderate, #ffc024);
  --pfe-broadcasted--color--text: var(--pfe-theme--color--feedback--moderate--lightest, #fffbf0);
}

:host([pfe-status="important"]) {
  --pfe-status--BackgroundColor:  var(--pfe-theme--color--feedback--important, #d73401);
  --pfe-broadcasted--color--text: var(--pfe-theme--color--feedback--important--lightest, #ffe1d8);
}

:host([pfe-status="critical"]) {
  --pfe-status--BackgroundColor:  var(--pfe-theme--color--feedback--critical, #bb0000);
  --pfe-broadcasted--color--text: var(--pfe-theme--color--feedback--critical--lightest, #ffeeee);
}

:host([pfe-status="success"]) {
  --pfe-status--BackgroundColor:  var(--pfe-theme--color--feedback--success, #2e7d32);
  --pfe-broadcasted--color--text: var(--pfe-theme--color--feedback--success--lightest, #f7fcec);
}

:host([pfe-status="info"]) {
  --pfe-status--BackgroundColor:  var(--pfe-theme--color--feedback--info, #0277bd);
  --pfe-broadcasted--color--text: var(--pfe-theme--color--feedback--info--lightest, #dcf5fe);
}

:host([pfe-status="warning"]) {
  --pfe-status--BackgroundColor:  var(--pfe-theme--color--feedback--moderate, #ffc024);
  --pfe-broadcasted--color--text: var(--pfe-theme--color--feedback--moderate--darkest, #8a6200);
}

:host([pfe-status="normal"]) {
  --pfe-status--BackgroundColor: var(--pfe-theme--color--ui-base, #0477a4);
  --pfe-broadcasted--color--text: var(--pfe-theme--color--ui-base--text, #fff);
}

:host([pfe-status="complement"]) {
  --pfe-status--BackgroundColor: var(--pfe-theme--color--ui-complement, #464646);
  --pfe-broadcasted--color--text: var(--pfe-theme--color--ui-complement--text, #fff);
}

:host([pfe-status="accent"]) {
  --pfe-status--BackgroundColor: var(--pfe-theme--color--ui-accent, #fe460d);
  --pfe-broadcasted--color--text: var(--pfe-theme--color--ui-accent--text, #fff);
}
